export default {
	data() {
		return {
			model: {
				truename: '',
				phone: '',
				idCard: '',
				isAdmin: false
			}
		};
	},
	methods: {
		handelChangeIsAdmin(e) {
			this.model.isAdmin = e.detail.value;
		}
	}
};
